export default class FotoService{
    constructor(resource){
        this._resource = resource('v1/fotos{/id}')
    }

    lista(){
        //A funo query busca os dado na API e retorna uma promesa, se os dados forem retornados com sucesso, eles sero convertidos para JSON e retornaram outra promesa quando o mtodo for chamado
        return this._resource.query().then(res => res.json(), err => {
            console.log(err)
            throw new Error("No foi possvel obter as fotos. Tente mais tarde")
        })
    }

    cadastra(foto){

        if(foto._id)
            return this._resource.update({id: foto._id}, foto) //O update atualiza os dados da API, recebendo como primeiro parametro o id da foto que ser alterada, e como segundo, recebe os dados que sero alterados
        else
            return this._resource.save(foto) //A funo save recebe os dados da foto como parametro e insere elas na API
    }

    apaga(id){
        // A funo delete recebe como parametro o id da foto que ser usado para apagar a foto na API e retorna um promesa que a foto ser excluida
        return this._resource.delete({id: id})
            .then(null, err => { //O delete no me retorna nada no then, ento defino null, mas pode me dar erro
                console.log(err)
                throw new Error("No foi possvel remover a foto")
            })
    }

    busca(id){
        return this._resource.get({ id }).then(res => res.json()) //Executando uma ao do tipo get para me retornar o item na API que possui o mesmo id informado no parametro do get
    }
}


